import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { apiCallBegan } from "./middleware/api"
import { AppThunk, RootState } from "./store"

type ModelOrUndefined = Model | undefined

//for custom selections eg. pick colours

export interface CustomOption {
  id: string
  option_name: string
  option_description?: string
  rrp?: number
  option_image?: Image[]
}
export interface CustomOptionPicker {
  id: string
  screen_name: string
  option_name: string
  options: CustomOption[]
}

export interface Category {
  id: string
  name: string
  icon: {
    url: string
  }
  accessories: Accessory[]
}
interface Image {
  url: string
  id: string
  width: number
  height: number
  alternativeText: string
}
export interface buildItem {
  id: string
  name: string
  short_description: string
  description?: string
  rrp?: number
  featured_image?: Image
}

export interface Product extends buildItem {
  models: Model[]
}

interface Accessory extends buildItem {
  option_for: [
    {
      id: string 
    }
  ]
  included_in: [
    {
      id: string 
    }
  ]
  accessory_categories: [
    {
      id: string 
      icon: {
        url: string
      }
    }
  ]
}

interface Model extends buildItem {
  included_accessories: [
    {
      id: string 
    }
  ]
  optional_accessories: [
    {
      id: string 
    }
  ]
}
interface catalog {
  products: Product[]
  // models: Model[];
  accessories: Accessory[]
  accessoriesLoading: boolean
  customOptionPickersLoading: boolean
  accessoryCategories: Category[]
  loading: boolean
  customOptionPickers: CustomOptionPicker[]
}

const initialState: catalog = {
  loading: false,
  accessoriesLoading: false,
  customOptionPickersLoading: false,
  products: [],
  // models: [],
  accessories: [],
  accessoryCategories: [],
  customOptionPickers: [],
}

export const catalogSlice = createSlice({
  name: "catalog",
  initialState,
  reducers: {
    customOptionPickersRequested: (state, action: PayloadAction<string>) => {
      state.customOptionPickersLoading = true
    },
    customOptionPickersReceived: (state, action: PayloadAction<any>) => {
      if (action.payload.model) {
        state.customOptionPickers = action.payload.model.other_options
      }
      state.customOptionPickersLoading = false
    },
    accessoriesRequested: (state, action: PayloadAction<string>) => {
      state.accessoriesLoading = true
    },
    accessoriesReceived: (state, action: PayloadAction<any>) => {
      state.accessoriesLoading = false
      state.accessories = action.payload.accessories
    },
    catalogRequested: (state, action: PayloadAction<string>) => {
      state.loading = true
    },
    catalogReceived: (state, action: PayloadAction<any>) => {
      console.log(action.payload.products)
      state.products = action.payload.products
      state.loading = false
    },
    categoriesRequested: (state, action: PayloadAction<string>) => {
      // state.loading = true;
    },
    categoriesReceived: (state, action: PayloadAction<any>) => {
      state.accessoryCategories = action.payload.accessoryCategories
    },
  },
})

export const loadCategories = (): AppThunk => (dispatch) => {
  const graphQlQuery = /*GRAPHQL*/ `{
    accessoryCategories {
      data {
      id 
      attributes {
         name
         accessories {
          data {
          	id
            attributes {
              name
              description
              short_description
              rrp
              featured_image {
                data{
                  id
                  attributes{
                  	url
                    alternativeText
                  }
                }
                
              }
              option_for {
                data{
                	id
                }
                
              }
            }
          }
      }
      }
    }
  }
  }`

  return dispatch(
    apiCallBegan({
      url: process.env.REACT_APP_GRAPHQL_URL + "",
      graphQlQuery,
      onStart: catalogSlice.actions.categoriesRequested.type,
      onSuccess: catalogSlice.actions.categoriesReceived.type,
    })
  )
}

export const loadCustomOptionPickers = (): AppThunk => (dispatch, getState) => {
  const modelId = getState().build.modelId || 0
  const graphQlQuery = /*GRAPHQL*/ `{
  model(id: "${modelId}") {
    other_options{
      id
      screen_name
      option_name
      options {	
        id
        option_name
        option_description
        rrp
        option_image {
          url
        }
      }
    }  
  }
}`

  return dispatch(
    apiCallBegan({
      url: process.env.REACT_APP_GRAPHQL_URL + "",
      graphQlQuery,
      onStart: catalogSlice.actions.customOptionPickersRequested.type,
      onSuccess: catalogSlice.actions.customOptionPickersReceived.type,
    })
  )
}


