export const loadCatalog = (): AppThunk => (dispatch) => {
  const graphQlQuery = /*GRAPHQL*/ `{
    products(sort: "rrp") {
      data {
        id
        attributes {
          name
          short_description
          description
          rrp
          featured_image {
            data{
              id
              attributes{
                url
                width
                height
              }
            }
          }
          models(sort: "rrp") {
            data{
              id
              attributes {
                name
                short_description
                description
                rrp
                featured_image{
                  data {
                    id 
                    attributes{
                      url
                    }
                  }
                }
              }
            }    
          }
        }     
      }
    }
  }`

  return dispatch(
    apiCallBegan({
      url: process.env.REACT_APP_GRAPHQL_URL + "",
      graphQlQuery,
      onStart: catalogSlice.actions.catalogRequested.type,
      onSuccess: catalogSlice.actions.catalogReceived.type,
    })
  )
}

export const loadAccessories = (): AppThunk => (dispatch) => {
  const graphQlQuery = /*GRAPHQL*/ `{
    accessories {
      data {
        id
        attributes{
          name
          short_description
          description
          rrp
          featured_image {
            data{
              attributes{
            		url    
              }
            }
          }
          accessory_categories{
            data{
              id
              attributes{
                name      
              }
            }
          }
          option_for {
            data{
            	id  
            }
          }
          included_in {
            data {
            	id  
            }
          }        
      }
    }   
  }
}`

  return dispatch(
    apiCallBegan({
      url: process.env.REACT_APP_GRAPHQL_URL + "",
      graphQlQuery,
      onStart: catalogSlice.actions.accessoriesRequested.type,
      onSuccess: catalogSlice.actions.accessoriesReceived.type,
    })
  )
}

// SELECTORS
export const selectProducts = (state: RootState) => state.catalog.products
export const selectCompatibleModels = (state: RootState) =>
  state.catalog.products.find((prod) => ""+state.build.productId === prod.id)
    ?.models
export const selectAccessoryCatById = (catId: string) => (state: RootState) => {
  return state.catalog.accessoryCategories.find((cat) => cat.id === catId)
}
export const selectIncludedAccessories = (state: RootState) => {
  return state.catalog.accessories.filter((acc) => {
    if (state.build.modelId) {
      const accessory = acc.included_in.find(
        (item) => item.id === state.build.modelId
      )
      return accessory
    } else {
      return false
    }
  })
}

export const selectOptionalAccessories = (state: RootState) => {
  return state.catalog.accessories.filter((acc) => {
    if (state.build.modelId) {
      const accessory = acc.option_for.find(
        (item) => item.id === state.build.modelId
      )
      return accessory
    } else {
      return false
    }
  })
}

export const selectOptionalAccessoriesInCat = (catId: string) => (
  state: RootState
) => {
  return state.catalog.accessories.filter((acc) => {
    if (state.build.modelId) {
      const isOption = acc.option_for.find(
        (item) => item.id === state.build.modelId
      )
      const isInCat = acc.accessory_categories.find((cat) => cat.id === catId)

      return isOption && isInCat
    } else {
      return false
    }
  })
}
export const selectModelById = (id: number) => (state: RootState) => {
  if (!state.catalog.products || state.catalog.products.length === 0) {
    return undefined
  }
  return state.catalog.products.reduce<ModelOrUndefined>((acc, prod) => {
    const model: Model | undefined = prod.models?.find(
      (model) => {
        console.log(typeof model.id, typeof id)

        return (model.id) === ""+id
      }
    )

    if (model) return model
    else return acc
  }, undefined)
}
export const selectProductById = (id: string) => (state: RootState) => {
  if (!state.catalog.products || state.catalog.products.length === 0) {
    console.log('no prod found', state.catalog.products)
    return undefined
  }
  // console.log(state.catalog.products)
  // console.log(state.catalog.accessories)
  const product:Product|undefined = state.catalog.products.find((prod) => {
    console.log(typeof prod.id, typeof id)
    return prod.id === id})
  return product
}

export const selectAccessoryById = (id: string) => (state: RootState) =>
  state.catalog.accessories.find((accessory) => accessory.id === ""+id)
export const selectCategories = (state: RootState) =>
  state.catalog.accessoryCategories
  

export const selectCustomOptionPicker = (id: string) => (state: RootState) =>
  state.catalog.customOptionPickers.find((el) => el.id === id)
export const selectCustomOptionPickers = (state: RootState) =>
  state.catalog.customOptionPickers
export const selectCustomOptionPickersLoading = (state: RootState) =>
  state.catalog.customOptionPickersLoading


export default catalogSlice.reducer