import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { apiCallBegan } from './middleware/api';
import { AppThunk, RootState } from "./store";


export interface CustomOptionEntry {
  optionId: string
  optionSelectionId: string
}
interface buildState {
  productId: string | undefined
  modelId: string | undefined
  accessoryIds: string[]
  customOptions: CustomOptionEntry[]
}

const initialState: buildState = {
  productId: undefined,
  modelId: undefined,
  accessoryIds: [],
  customOptions: [],
}

export const buildSlice = createSlice({
  name: "build",
  initialState,
  reducers: {
    setCustomOption: (state, action: PayloadAction<CustomOptionEntry>) => {
      //find option and update it, or add it if it is new.
      const idx = state.customOptions.findIndex(
        (el) => el.optionId === action.payload.optionId
      )

      if (idx !== -1) {
        state.customOptions[idx].optionSelectionId =
          action.payload.optionSelectionId
      } else state.customOptions.push(action.payload)
    },
    addAccessory: (state, action: PayloadAction<string>) => {
      state.accessoryIds.push(action.payload)
    },
    setProduct: (state, action: PayloadAction<string>) => {
      if (state.productId !== action.payload) {
        state.modelId = undefined
      }
      state.productId = action.payload
    },
    setModel: (state, action: PayloadAction<string>) => {
      if (state.modelId !== action.payload) {
        state.accessoryIds = []
      }
      state.modelId = action.payload
    },
    removeAccessory: (state, action: PayloadAction<string>) => {
      const idx = state.accessoryIds.indexOf(action.payload)
      if (idx > -1) {
        const newState = state.accessoryIds.splice(idx, 1)
        console.log(newState)
      }
    },
    buildPosted: () => {},
  },
})

export const postBuild = (): AppThunk => (dispatch) => {
  const graphQlQuery =  `{
    accessoryCategories {
      data{
        id
        attributes{
          name          
          accessories {
            data {
              attributes {
                name
                rrp
                short_description
                description
              }
            }
          }
        }
    }
  }
}`

  return dispatch(
    apiCallBegan({
      url: process.env.REACT_APP_GRAPHQL_URL + "",
      graphQlQuery,
      onSuccess: buildSlice.actions.buildPosted.type,
    })
  )
}

export const callWebhook = (url:string, data:any): AppThunk => (dispatch) => {
  console.log('webkoook')
  return dispatch(
    apiCallBegan({
      url: url,
      data:data
    })
  )
}

export const {
  setCustomOption,
  addAccessory,
  setProduct,
  setModel,
  removeAccessory,
} = buildSlice.actions

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const selectAccessoryById = (state: RootState, id: string) =>
  state.catalog.accessories.find((acc) => acc.id === ""+id)
export const selectBuildAccessoryIds = (state: RootState) =>
  state.build.accessoryIds
export const selectBuildProduct = (state: RootState) =>
  state.catalog.products?.find((el) => el.id === ""+state.build?.productId)
export const selectBuildModel = (state: RootState) =>
  selectBuildProduct(state)?.models?.find((el) => el.id === ""+state.build.modelId)
export const selectBuildAccessories = (state: RootState) =>
  state.build.accessoryIds.map((id) =>
    state.catalog.accessories.find((el) => el.id === ""+id)
  )
export const selectBuild = (state: RootState) => {
  return {
    product: selectBuildProduct(state),
    model: selectBuildModel(state),
    accessories: selectBuildAccessories(state),
    
  }
}
export const selectBuildImage = (state: RootState) => {
  const modelImg = selectBuildModel(state)?.featured_image
  const prodImg = selectBuildProduct(state)?.featured_image
  if (modelImg) {
    return modelImg
  } else {
    return prodImg
  }
}
export const selectBuildCost = (state: RootState) => {
  const productPrice = selectBuildProduct(state)?.rrp || 0
  const modelPrice = selectBuildModel(state)?.rrp || 0
  const accessoryPrice = selectBuildAccessories(state).reduce(
    (acc, curr) => acc + (curr?.rrp || 0),
    0
  )
  if (modelPrice) {
    return modelPrice + accessoryPrice
  } else if (productPrice) {
    return productPrice
  } else {
    return 0
  }
}

// give it id of an option picker and it will return
// an object with the selected option from the option picker
export const selectBuildCustomOption = (id: string) => (state: RootState) => {
  //for some reason the param id is a string... so careful of comparisons..
  const buildOption = state.build.customOptions.find((option) => {
    return option.optionId === id
  })
  const selectedOptionId = buildOption?.optionSelectionId

  //could have an issue with this, but not with current ui design
  if (state.catalog.customOptionPickersLoading) {
    return undefined
  }

  const catalogOption = state.catalog.customOptionPickers.find(
    (picker) => picker.id === id
  )

  return catalogOption?.options.find((option) => option.id === selectedOptionId)
}

//work out how to return all the options iwth their names
/*[
  {
    name: 'Colour',
    option: {
      id,
      name
    }
  }
]*/
//only returns the selected options
export const selectBuildCustomOptions = (state: RootState) => {
  const buildOptions = state.catalog.customOptionPickers.map((optionPicker) => {
    return {
      name: optionPicker.option_name,
      selectedOption: selectBuildCustomOption(optionPicker.id)(state),
    }
  })

  return buildOptions
}

export const selectFullBuild = (state: RootState) => {
  
  const buildproduct = selectBuildProduct(state)
  const buildmodel = selectBuildModel(state)
  const buildaccessories = selectBuildAccessories(state)
  const buildimage = selectBuildImage(state)

  return {
    product: buildproduct,
    model: buildmodel,
    accessories: buildaccessories,
    image: buildimage
  }
}
export default buildSlice.reducer
