import cx from "classnames"
import React, { useState } from "react"
import { VscCreditCard, VscMail } from "react-icons/vsc"
import { useSelector } from "react-redux"
import {
  selectBuildAccessories,
  selectBuildCost, selectBuildImage, selectBuildModel,
  selectBuildProduct,
  selectFullBuild
} from "../store/build"
import { selectCustomerDetails } from "../store/customer"
import { formatCurrency } from "../utils/utils"
import "./ThankYouScreen.css"

export default function ThankYouScreen() {
  const product = useSelector(selectBuildProduct)
  const model = useSelector(selectBuildModel)
  const accessories = useSelector(selectBuildAccessories)  
  const image = useSelector(selectBuildImage)

  const [emailSent, setEmailSent] = useState(false);
  const allDetails = {
    ...useSelector(selectFullBuild), 
    customer: useSelector(selectCustomerDetails)
  };
  const sendMail = () =>{

    if(window.szReactPlugin && emailSent===false){
      fetch(window.szReactPlugin?.email_endpoint, {

        method: "POST",
        // credentials: 'same-origin',
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(allDetails)
      })
        .then((response) => response.json())
        .then((data) => {
          setEmailSent(true);
          if (data) {
            console.log(data)
          }
        })
        .catch((error) => {
          console.error(error);
          setEmailSent(false);
        });
    }
  }

  return (
    <div className="thankyou-wrapper">
      <h1 className="h1">Thank you for your enquiry</h1>
      <h3 className="thankyou-productname"><span className="thankyou-prod-name">{product?.name}</span> {model?.name}</h3>
      {accessories.length > 0 && <span className="thankyou-accessories">With {accessories.length} Accessories</span>}
      <div className="thankyou-product-image">
        <img src={"" + process.env.REACT_APP_ASSETS_URL + image?.url} alt="" />
      </div>
      <div className="thankyou-box-price">
        Total <span>{formatCurrency(useSelector(selectBuildCost))}</span>
      </div>
      <div className="thankyou-buttons">
        <button className={cx("outline-button secondary", {disabled: emailSent===true})} onClick={sendMail}><VscMail/> <span>Email Me a Copy</span></button>
        <button className="primary-button"><VscCreditCard/> <span>Checkout</span></button>
      </div>

    </div>
  )
}
