import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "./store";
export type deliveryDetails = {
  deliveryMethod: string
  dealer: string
}

export interface address {
  line1: string
  line2?: string
  city: string
  country: string
  state: string
  postcode: string
}

export interface customerDetails {
  firstName?: string
  surname?: string
  phone?: string
  email?: string
  address?: address
  newsletter?: boolean
  deliveryMethod?: string
  preferredDealer?: string
}

const initialState: customerDetails = {}

export const customerSlice = createSlice({
  name: "customer",
  initialState,
  reducers: {
    updateDeliveryDetails: (state, action: PayloadAction<deliveryDetails>) => {
      return { ...state, ...action.payload }
    },
    updateCustomerDetails: (state, action: PayloadAction<customerDetails>) => {
      return { ...state, ...action.payload }
    },
    updateCustomerAddress: (state, action: PayloadAction<address>) => {
      state.address = action.payload
    },
  },
})

export const {
  updateDeliveryDetails,
  updateCustomerDetails,
  updateCustomerAddress,
} = customerSlice.actions

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
// export const selectBuilderStep = (state: RootState) => state.ui.builderStep
export const selectCustomerDetails= (state: RootState) =>{
  return state.customer
}

export default customerSlice.reducer
