import React from "react"
import { useDispatch, useSelector } from "react-redux"
import { selectModelById } from "../store/catalog"
import { closeProductDetailsScreen, nextBuilderStep, selectDisplayedId } from "../store/ui"
import { formatCurrency } from "../utils/utils"
import "./App.css"
import FooterNav from "./FooterNav"
import "./ModelDetails.css"

export default function ModelDetails() {
  
  const id = useSelector(selectDisplayedId)
  const model = useSelector(selectModelById(parseInt(id)))
  const dispatch = useDispatch()
  console.log(id)
  return (
    <>
      <div className="model-details-section">
        <div className="model-image-container">
          {model && <img src={"" + process.env.REACT_APP_ASSETS_URL + model?.featured_image?.url} alt="" />}
        </div> 
        <div className="model-details">
          <h3>{model && model?.name}</h3>
          <h5 className="model-price">{model?.rrp && formatCurrency(model.rrp)}</h5>
          <p>{model && model.description}</p>
        </div>
      </div>
      <FooterNav 
        onNextClick={() => {dispatch(nextBuilderStep()); }}
        onPrevClick={() => {dispatch(closeProductDetailsScreen()); }}
        showNext={true}
        showPrev={true}
        prevEnabled={true}
        nextEnabled={true} 
        nextLabel={"Select " + model?.name + " Model"}
        prevLabel="Back" />
      
    </>
  )
}
