import cx from "classnames"
import React, { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { callWebhook, selectBuild, selectBuildModel, setModel } from "../store/build"
import { selectCompatibleModels } from "../store/catalog"
import { selectSettings } from "../store/settings"
import { showModelDetailsScreen } from "../store/ui"
import { formatCurrency } from "../utils/utils"
import "./ModelPicker.css"


interface props {
  onValidChange: (isValid: boolean) => void
}

export default function ModelPicker(props: props) {
  const dispatch = useDispatch()

  const models = useSelector(selectCompatibleModels)
  const model = useSelector(selectBuildModel)
  const build = useSelector(selectBuild)
  const settings = useSelector(selectSettings)


  //if one of these models is selected, all progress
  useEffect(() => {
    if (models?.find((prod) => prod.id === model?.id)) {
      props.onValidChange(true)
    } else {
      props.onValidChange(false)
    }
  })
  let buildItemsMarkup
  
  
  if (models) {
    buildItemsMarkup = models.map((x) => {
      return (
        <div
          className={cx("product", { selected: model?.id === x.id })}
          key={x.id}
          onClick={() => {
            dispatch(setModel(x.id))
            // any click make this valid. if validation is more complex,
            // you can add some state if nececssary
            console.log('called')
            props.onValidChange(true)
            if( settings?.webhook_url){
              dispatch(callWebhook(settings?.webhook_url, build))
            }
          }}
        >

          <div className="product-thumb-wrapper">
            {x.featured_image?.url && (
              <img
                className="product-thumb"
                src={process.env.REACT_APP_ASSETS_URL + x.featured_image?.url}
                alt={x.name}
              />
            )}
          </div>
          <div className="select-product-details">
            <h3 className="product-title">{x.name}</h3>
            <p className="product-short_description">{x.short_description}</p>
            <div className="product-bottom-line">
              {x.rrp && <h4 className="product-price">Starts at {formatCurrency(x.rrp)}</h4>}
              <button className="secondary-link text-button" onClick={()=>dispatch(showModelDetailsScreen(x.id))}>View Specs</button>
            </div>

          </div>
        </div>
      )
    })
  }

  return (
    <div className="product-listing">
      {buildItemsMarkup}
    </div>
  )
}
