import React, { useEffect } from "react"
import { useForm } from "react-hook-form"
import { useDispatch } from "react-redux"
import { deliveryDetails, updateDeliveryDetails } from "../store/customer"
import "./form.css"
interface props {
  onValidChange: (isValid: boolean) => void
}

export default function DeliveryDetailsForm(props: props) {
  const dispatch = useDispatch()
  const { register, triggerValidation, getValues } = useForm<deliveryDetails>({
    mode: "onChange",
    reValidateMode: "onChange",
  })

  useEffect(() => {
    props.onValidChange(false)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  
  const handleChange = async () => {
    dispatch(updateDeliveryDetails(getValues()))
    const result = await triggerValidation()
    props.onValidChange(result)
  }

  return (
    <React.Fragment>
      <form onChange={handleChange} className="szForm">
        <fieldset>
          <legend>Delivery Method</legend>
          <label htmlFor="delivery" className="no-bg radio">
            <input
              id="delivery"
              name="deliverymethod"
              type="radio"
              value="delivery"
              ref={register}
            />
            <span className="label-text">Delivery</span>
          </label>

          <label htmlFor="pickup" className="no-bg radio">
            <input
              id="pickup"
              name="deliverymethod"
              type="radio"
              value="pickup"
              ref={register({
                validate: () => {
                  return getValues().deliveryMethod !== ""
                },
              })}
            />
            <span className="label-text">Pick Up</span>
          </label>
        </fieldset>

        <fieldset>
          <legend>Choose your dealer</legend>
          <select name="dealer" id="dealer" ref={register}>
            <option value="Head Office">Head Office</option>
            <option value="QLD Dealer"> QLD Dealer</option>
            <option value="NSW Dealer"> NSW Dealer</option>
          </select>
        </fieldset>
      </form>
    </React.Fragment>
  )
}
