import { createAction, Middleware } from "@reduxjs/toolkit"
import { GraphQLClient } from "graphql-request"
import { normalize } from "../../utils/utils"

interface QueryVars {
  key: string
  value: string
}

interface QueryVarsObj {
  [key: string]: QueryVars
}

interface apiPayload {
  url: string
  graphQlQuery?: string
  // method?: string
  data?: string
  onSuccess?: string
  onStart?: string
  onError?: string
}


export const apiCallBegan = createAction<apiPayload>("api/CallBegan")
export const apiCallSuccess = createAction<any>("api/CallSuccess")
export const apiCallFailed = createAction<string>("api/CallFailed")

const api: Middleware = ({ dispatch }) => (next) => async (action) => {

  console.log(action.type)
  if (action.type !== apiCallBegan.type) return next(action)
  next(action)

  const {
    url,
    graphQlQuery,
    data,
    onStart,
    onSuccess,
    onError,
    
  } = action.payload
  if (onStart) dispatch({ type: onStart })
  
  try {
    let response = undefined;
    //used to call webhooks
    if (!graphQlQuery) {
      response = await fetch(url, {
        method: 'POST', // *GET, POST, PUT, DELETE, etc.
        mode: 'no-cors', // no-cors, *cors, same-origin
        cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
        headers: {
          'Content-Type': 'application/json'
          // 'Content-Type': 'application/x-www-form-urlencoded',
        },
        redirect: 'follow', // manual, *follow, error
        body: JSON.stringify(data) // body data type must match "Content-Type" header
      });
    
    }

    if(graphQlQuery){
      const graphQLClient = new GraphQLClient(url, {
        // headers: {
        //   authorization: 'Bearer MY_TOKEN',
        // },
      })

      response = await graphQLClient.request(graphQlQuery)
    }

    // General
    // dispatch(apiCallSuccess(response))
    // Specific
    if (onSuccess){
      if(response && graphQlQuery){
        const strapi3datastyle = normalize(response as any)
        dispatch({ type: onSuccess, payload: strapi3datastyle })
      }

    }
  } catch (error) {
    
    console.log('exception '+error)
    // General
    // dispatch(apiCallFailed(error.message))
    // Specific
    
    if (onError) dispatch({ type: onError, payload: (error as any).message })
  }
}

export default api
