import React, { useEffect } from "react"
import { useForm } from "react-hook-form"
import { useDispatch } from "react-redux"
import { address, updateCustomerAddress } from "../store/customer"
import "./form.css"
interface props {
  onValidChange: (isValid: boolean) => void
}

export default function AddressForm(props: props) {
  const dispatch = useDispatch()
  const { register, getValues, triggerValidation } = useForm<address>()
  
  //todo - fix
  useEffect(() => {
    props.onValidChange(false)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleChange = async () => {
    dispatch(updateCustomerAddress(getValues()))
    const result = await triggerValidation()
    props.onValidChange(result)
  }

  return (
    <React.Fragment>
      <form onChange={handleChange} className="szForm">
        <fieldset>
          <legend>Enter your address</legend>
          <label htmlFor="address-line-1">
            <span className="label-text">Line 1</span>
            <input
              id="address-line-1"
              name="address-line-1"
              placeholder="11 Smith St"
              ref={register({ required: true })}
            />
          </label>

          <label htmlFor="address-line-2">
            <span className="label-text">Line 2</span>
            <input
              id="address-line-2"
              name="address-line-2"
              placeholder=""
              ref={register}
            />
          </label>

          <label htmlFor="city">
            <span className="label-text">City</span>
            <input
              id="city"
              name="city"
              placeholder="Melbourne"
              ref={register({ required: true })}
            />
          </label>

          <label htmlFor="country">
            <span className="label-text">Country</span>
            <input
              id="country"
              name="country"
              defaultValue="Australia"
              placeholder="Australia"
              ref={register({
                required: true,
              })}
            />
          </label>

          <label htmlFor="state">
            <span className="label-text">State</span>
            <input
              id="state "
              name="state"
              placeholder="VIC"
              ref={register({ required: true })}
            />
          </label>
        </fieldset>
      </form>
    </React.Fragment>
  )
}
