export const formatCurrency = ( value: number, includeCents:boolean = false ): string => {
  
  if(includeCents){
    
  
    // Create our number formatter.
    var formatter = new Intl.NumberFormat('en-AU', {
      style: 'currency',
      currency: 'AUD',
    });
    return formatter.format(value); /* $2,500.00 */
  } else {
    return "$" + Number(value).toLocaleString()

  }

}
export const truncate = (text:string, index:number) => {
  if(text.length > index) {
    return text.substring(0, index) + "\u2026";
  }
  return text;
}

/* takes strapiv4 response data and changes the structure back to strapiv3 style */
/* ie. changes
products{
  data{
    id   
    attributes{
      name
    }
  }
}
to
products{
  id
  name
}*/
export const normalize = (data:any) => {
  const isObject = (data:any) =>
    Object.prototype.toString.call(data) === '[object Object]'
  const isArray = (data:any) =>
    Object.prototype.toString.call(data) === '[object Array]'

  const flatten = (data:any) => {
    if (!data.attributes) return data

    return {
      id: data.id,
      ...data.attributes
    }
  }

  if (isArray(data)) {
    return data.map((item:any) => normalize(item))
  }

  if (isObject(data)) {
    if (isArray(data.data)) {
      data = [...data.data]
    } else if (isObject(data.data)) {
      data = flatten({ ...data.data })
    } else if (data.data === null) {
      data = null
    } else {
      data = flatten(data)
    }

    for (const key in data) {
      data[key] = normalize(data[key])
    }

    return data
  }

  return data
}
