import React from "react"
import StepTitle from "./StepTitle"

interface StepTitlesProps {
    titles: string[],
    activeStep: string
}
export default function StepTitles (props:StepTitlesProps) {

  const stepTitles = props.titles.map( (title, idx) => 
    <StepTitle title={idx+1 + ". " + title} isActive={title===props.activeStep} key={idx} />)

  return <>{stepTitles}</> 

}
