import React from "react"
import { FiShoppingBag } from "react-icons/fi"
import { useSelector } from "react-redux"
import {
  selectBuildAccessories,
  selectBuildCost,
  selectBuildCustomOptions, selectBuildModel,
  selectBuildProduct
} from "../store/build"
import { formatCurrency } from "../utils/utils"
import "./BuildStatusBox.css"


export default function BuildStatusBox() {
  const product = useSelector(selectBuildProduct)
  const model = useSelector(selectBuildModel)
  const accessories = useSelector(selectBuildAccessories)
  const selectedOptions = useSelector(selectBuildCustomOptions)

  const customOptionsMarkup = selectedOptions.map((option) => (
    <div key={option.name}>
      <span>{option.name}</span>
      <span>{option.selectedOption?.option_name}</span>
    </div>
  ))

  const productTitle = model?.name ?  model?.name : product?.name

  return (
    <div className="build-box">
      <div className="build-box-left">
        <div className="build-box-icon">
          <FiShoppingBag />
        </div>
        <div className="build-box-text">
          <h3 className="build-box-title">{productTitle ? productTitle : "Select Product"}</h3>
          <span className="build-box-accessories">{accessories.length} Accessories</span>
        </div>

      </div>
      <div className="build-box-price">
        <span>{formatCurrency(useSelector(selectBuildCost))}</span>
      </div>
      {customOptionsMarkup}
    </div>
  )
}
