import cx from "classnames";
import React, { useContext, useRef } from "react";
import { VscChevronDown } from "react-icons/vsc";
import "./Accordion.css";

interface AccordionProps  {
    children:React.ReactNode
}
interface AccordionItemProps extends React.HTMLAttributes<HTMLElement> {
    title:string
    children:React.ReactNode
    index?: number
}
interface AccordionContext {
    openAccordionItem: number,
    setOpenAccordionItem: React.Dispatch<React.SetStateAction<number>> | undefined
}

const AccordionContext = React.createContext<AccordionContext>({
  openAccordionItem: 0,
  setOpenAccordionItem: undefined
})

export default function Accordion(props:AccordionProps) {
  const [openAccordionItem, setOpenAccordionItem] = React.useState(0)
  const children = React.Children.map(props.children, (child,idx) => {
    if (React.isValidElement(child)) {
      return React.cloneElement(child, { index: idx });
    }
  })
  return (
    <AccordionContext.Provider value={{openAccordionItem, setOpenAccordionItem}}>
      <div className="sz-accordion">
        {children}
      </div>
    </AccordionContext.Provider>
  )
}

export function AccordionItem(props:AccordionItemProps){
  const contentEl = useRef<HTMLDivElement>(null);
  const {openAccordionItem, setOpenAccordionItem} = useContext(AccordionContext)
  const isOpen = props.index === openAccordionItem

  return (
    <div {...props} className={cx("accordion-item", {open: isOpen})}>
      <div className="accordion-item-header" onClick={() => setOpenAccordionItem && setOpenAccordionItem(isOpen ? -1 : (props.index || 0))}>
        <h3 className="accordion-item-title">{props.title}</h3>
        <div className="svg-icon-container">
          <VscChevronDown />
        </div>
      </div>
      <div className="accordion-content-wrapper" style={
        isOpen
          ? { maxHeight: contentEl?.current?.scrollHeight }
          : { maxHeight: "0px" }
      }>
        <div className="accordion-item-content" ref={contentEl}>
          {props.children}
        </div>
      </div>
    </div>
  )
}