import cx from "classnames"
import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { animated, config, useTransition } from 'react-spring'
import { nextBuilderStep, prevBuilderStep, selectCurrentStep, selectCurrentUiScreen, selectScreenOrder, UIScreenTitles } from "../store/ui"
import AccessoriesPicker from "./AccessoriesPicker"
import AddressForm from "./AddressForm"
import "./App.css"
import BuildStatusBox from "./BuildStatusBox"
import ContactDetailsForm from "./ContactDetailsForm"
import DeliveryDetailsForm from "./DeliveryDetailsForm"
import FooterNav from "./FooterNav"
import ModelDetails from "./ModelDetails"
import ModelPicker from "./ModelPicker"
import ProductDetails from "./ProductDetails"
import ProductPicker from "./ProductPicker"
import StepTitles from "./StepTitles"
import ThankYouScreen from "./ThankYouScreen"

declare global {
  interface Window {szReactPlugin:any}
}
function App() {
  const currentScreen     = useSelector(selectCurrentUiScreen);
  const currentScreenIdx  = useSelector(selectCurrentStep);
  const screenNames       = useSelector(selectScreenOrder);
  const dispatch          = useDispatch();
  const [canAdvance, setCanAdvance] = useState(true);
  const transition = useTransition(currentScreen, {
    from: { transform: 'translateX(10%)', opacity:0 },
    enter: { opacity: 1, transform: 'translateX(0%)' },
    leave: { opacity: 0, transform: 'translateX(-10%)' },
    config: config.slow
    // delay: 1000,
  })

  const handleValidChange = (isValid:boolean) => {
    setCanAdvance(isValid);
  }


  const getScreen = (screen: string) => {
    switch(screen){
    case UIScreenTitles.SELECT_PRODUCT:
      return <ProductPicker onValidChange={handleValidChange} />
    case UIScreenTitles.SELECT_MODEL:
      return <ModelPicker onValidChange={handleValidChange} />
    case UIScreenTitles.ADD_ACCESSORIES:
      return <AccessoriesPicker  />
    case UIScreenTitles.YOUR_DETAILS:
      return <ContactDetailsForm onValidChange={handleValidChange} />
    case UIScreenTitles.DELIVERY_DETAILS:
      return <DeliveryDetailsForm onValidChange={handleValidChange} />
    case UIScreenTitles.YOUR_ADDRESS:
      return <AddressForm onValidChange={handleValidChange} />
    case UIScreenTitles.CONFIRMATION:
      return <ThankYouScreen/>
    case UIScreenTitles.MODEL_DETAILS:
      return <ModelDetails/>
    case UIScreenTitles.PRODUCT_DETAILS:
      return <ProductDetails/>
    }
  }
  useEffect(()=> {

  }, [currentScreen]);
  return (
    <div className="app">
      <div className="app-container">
        <div className="primary-container">
          <main>
            <header>
              <h1 className="small-caps">Configure Your Camper</h1>
              <BuildStatusBox />
            </header>
            <div className="step-title-container">
              <StepTitles titles={screenNames} activeStep={currentScreen} />
            </div>
            <div  className="current-screen-container">
              {transition((style, theCurrentScreen, t, ) => 
                <animated.div style={style} className={cx("item", {active: theCurrentScreen === currentScreen})}>
                  {getScreen(theCurrentScreen)}
                </animated.div> 
              )}
            </div>
            {(currentScreen !== UIScreenTitles.MODEL_DETAILS &&
              currentScreen !== UIScreenTitles.PRODUCT_DETAILS) &&
            <FooterNav 
              onNextClick={() => {dispatch(nextBuilderStep()); setCanAdvance(true); }}
              onPrevClick={() => {dispatch(prevBuilderStep()); setCanAdvance(true); }}
              showNext={currentScreenIdx < screenNames.length-1}
              showPrev={true}
              prevEnabled={currentScreenIdx !== 0 }
              nextEnabled={canAdvance} 
              nextLabel={screenNames[currentScreenIdx+1]}
              prevLabel="Back" />
            }
          </main>
        </div>
      </div>
    </div>
  )
}
          
export default App
