import React, { useEffect } from "react"
import { useForm } from "react-hook-form"
import { useDispatch } from "react-redux"
import { customerDetails, updateCustomerDetails } from "../store/customer"
import "./form.css"

interface props {
  onValidChange: (isValid: boolean) => void
}

export default function ContactDetailsForm(props: props) {
  const dispatch = useDispatch()
  const { register, errors, getValues, triggerValidation } = useForm<
    customerDetails
  >({
    mode: "onChange",
  })

  useEffect(() => {
    props.onValidChange(false)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleChange = async () => {
    dispatch(updateCustomerDetails(getValues()))
    const result = await triggerValidation()
    props.onValidChange(result)
  }

  return (
    <React.Fragment>
      <form onChange={handleChange} className="szForm">
        <fieldset>
          <legend>Enter your contact details</legend>
          <label htmlFor="firstName">
            <span className="label-text">First Name</span>
            <input
              id="firstName"
              name="firstName"
              placeholder="John"
              ref={register({ required: true })}
            />
          </label>

          <label htmlFor="surname">
            <span className="label-text">Surname</span>
            <input
              id="surname"
              name="surname"
              placeholder="Smith"
              ref={register({ required: true })}
            />
          </label>

          <label htmlFor="phone">
            <span className="label-text">Phone</span>
            <input
              id="phone"
              name="phone"
              placeholder="0400 000 000"
              ref={register({ required: true })}
            />
          </label>

          <label htmlFor="email">
            <span className="label-text">Email</span>
            <input
              id="email"
              name="email"
              placeholder="your@email.com"
              ref={register({
                required: true,
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                  message: "Invalid email address",
                },
              })}
            />
          </label>
          {errors.email && <span>{errors.email.message}</span>}

          <label htmlFor="postcode">
            <span className="label-text">Postcode</span>
            <input
              id="postcode "
              name="postcode"
              placeholder="3000"
              ref={register({ required: true })}
            />
          </label>

          <label htmlFor="newsletter" className="no-bg">
            <input
              id="newsletter"
              type="checkbox"
              name="newsletter"
              defaultChecked={true}
              ref={register}
            />
            Sign up for special offers and promotions
          </label>
        </fieldset>
      </form>
    </React.Fragment>
  )
}
