import React from "react"
import ReactMarkdown from "react-markdown"
import { useDispatch, useSelector } from "react-redux"
import { selectProductById } from "../store/catalog"
import { closeProductDetailsScreen, nextBuilderStep, selectDisplayedId } from "../store/ui"
import { formatCurrency } from "../utils/utils"
import FooterNav from "./FooterNav"
import "./ProductDetails.css"

export default function ProductDetails() {

  const id = useSelector(selectDisplayedId)
  const product = useSelector(selectProductById(id))
  const dispatch = useDispatch()

  
  return (
    <>
      <div className="product-details-section">
        <div className="model-image-container">
          {product && <img src={"" + process.env.REACT_APP_ASSETS_URL + product?.featured_image?.url} alt="" />}
        </div>  
        <div className="product-details">
          <h3>{product && product?.name}</h3>
          <h5 className="product-price">{product?.rrp && formatCurrency(product.rrp)}</h5>
          <ReactMarkdown>{product?.description +""}</ReactMarkdown>
        </div>
      </div>
      <FooterNav 
        onNextClick={() => {dispatch(nextBuilderStep()); }}
        onPrevClick={() => {dispatch(closeProductDetailsScreen()); }}
        showNext={true}
        showPrev={true}
        prevEnabled={true}
        nextEnabled={true} 
        nextLabel={"Select " + product?.name + " Model"}
        prevLabel="Back" />
      
    </>
  )
}
