import {
  Action,
  configureStore,
  getDefaultMiddleware,
  ThunkAction
} from "@reduxjs/toolkit"
import buildReducer from "./build"
import catalogReducer, {
  loadAccessories,
  loadCatalog,
  loadCategories
} from "./catalog"
import customerReducer from "./customer"
import api from "./middleware/api"
import settingsReducer, { loadSettings } from "./settings"
import uiReducer from "./ui"

export const store = configureStore({
  reducer: {
    catalog: catalogReducer,
    build: buildReducer,
    ui: uiReducer,
    settings: settingsReducer,
    customer: customerReducer,
  },
  middleware: [...getDefaultMiddleware(), api],
})

//cast to allow for dispatching thunkactions
store.dispatch<any>(loadCatalog())
store.dispatch<any>(loadAccessories())
store.dispatch<any>(loadCategories())
store.dispatch<any>(loadSettings())
// store.dispatch<any>(loadCustomOptionPickers(1))

export type RootState = ReturnType<typeof store.getState>
export type AppDispatch = typeof store.dispatch
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>
