import { createSlice, PayloadAction } from "@reduxjs/toolkit"
import { apiCallBegan } from "./middleware/api"
import { AppThunk, RootState } from "./store"

interface settings {
  // settings:
  loading: boolean
  settings: {
    webhook_url: string | undefined
    require_user_contact_details_upfront: string | undefined
    logo_light: {
      url: string | undefined
    }
  }
}

const initialState: settings = {
  loading: false,
  settings: {
    webhook_url: undefined,
    require_user_contact_details_upfront: undefined,
    logo_light: {
      url: undefined,
    },
  },
}

export const settingsSlice = createSlice({
  name: "settings",
  initialState,
  reducers: {
    settingsRequested: (state, action: PayloadAction<string>) => {
      state.loading = true
    },
    settingsReceived: (state, action: PayloadAction<any>) => {
      state.settings = action.payload.settings
      state.loading = false
    },
  },
})

export const loadSettings = (): AppThunk => (dispatch) => {
  const graphQlQuery = /*GRAPHQL*/ `{
    settings: config {
      data{
        attributes{
          webhook_url
      		require_user_contact_details_upfront
          logo_light {
            data{
              attributes{
                url
              }
            }
          }
        }
      }
    }
  }`

  return dispatch(
    apiCallBegan({
      url: process.env.REACT_APP_GRAPHQL_URL + "",
      graphQlQuery,
      onStart: settingsSlice.actions.settingsRequested.type,
      onSuccess: settingsSlice.actions.settingsReceived.type,
    })
  )
}

export const selectSettings = (state: RootState) => state.settings.settings

export default settingsSlice.reducer
