import cx from "classnames";
import React from "react";
import { VscArrowRight } from "react-icons/vsc";

interface FooterNavProps {
    onNextClick: ()=>void,
    onPrevClick: ()=>void,
    showPrev: boolean,
    showNext: boolean,
    prevEnabled: boolean,
    nextEnabled: boolean,
    prevLabel: string,
    nextLabel: string
}
export default function FooterNav(props:FooterNavProps){

  return (
    <footer className="wizard-footer">
      {props.showPrev &&
      <button className={cx("back-button  secondary-button", { disabled: !props.prevEnabled })}
        onClick={props.onPrevClick}
      >{props.prevLabel}</button>
      }
      {props.showNext &&
    <button
      className={cx("primary-button ", { disabled: !props.nextEnabled })}
      onClick={props.onNextClick}>{props.nextLabel} <VscArrowRight/></button>

      }
    </footer>
  );
}
