import cx from "classnames";
import React from "react";
import { VscAdd, VscCheck, VscChevronRight, VscRemove } from "react-icons/vsc";
import { useDispatch, useSelector } from "react-redux";
import { addAccessory, removeAccessory, selectBuildAccessoryIds } from "../store/build";
import { selectCategories } from "../store/catalog";
import { formatCurrency, truncate } from "../utils/utils";
import "./AccessoriesList.css";
import Accordion, { AccordionItem } from "./Accordion";


interface AccessoriesListProps {
  // onDetailsClick: (id: string) => void,
  onDetailsClick: (id: string) => void
}

export default function AccessoriesList(props:AccessoriesListProps) {

  const cats = useSelector(selectCategories)
  const buildAccessories = useSelector(selectBuildAccessoryIds)
  const dispatch = useDispatch()

  const catsMarkup = cats.map((cat) => {
    const accessoryMarkup = cat.accessories.map(accessory=>{
      return (
        <div className="accessory-add-button-wrapper" key={accessory.id}>
          <div  className="accessory-add-button" >
            {accessory.featured_image &&
          <div className="accessory-button-thumb" onClick={()=> props.onDetailsClick(accessory.id)}>
            <img className="accessory-thumb" src={"" + process.env.REACT_APP_ASSETS_URL + accessory.featured_image.url} alt="" />
          </div>}
            <div className="accessory-button-info-wrapper">
              <div className="accessory-button-label" title={accessory.name} onClick={()=> props.onDetailsClick(accessory.id)}>
                <span className="accessory-button-title">{truncate(accessory.name, 44)}</span>
                <span className="accessory-button-subtitle">{formatCurrency(accessory.rrp || 0)}</span>
              </div>
              {buildAccessories.find(accId => accId === accessory.id) ?
                <span className="accessory-button-icon accessory-button-icon-added"  onClick={() => dispatch(removeAccessory(accessory.id))}>
                  <span className="accessory-button-icon-remove">
                    <VscRemove />
                  </span>
                  <span className="accessory-button-icon-check">
                    <VscCheck />
                  </span>
                </span> 
                :
                <span className="accessory-button-icon accessory-button-icon-add" onClick={() => dispatch(addAccessory(accessory.id))}>
                  <VscAdd />
                </span> 
              }
              <span className="accessory-button-icon accessory-button-icon-more-info" onClick={()=> props.onDetailsClick(accessory.id)}>
                <VscChevronRight />
              </span> 
            </div>
          </div>
        </div>
      )
    })
    
    return (
      <AccordionItem key={cat.id} title={cat.name}>
        {accessoryMarkup}
      </AccordionItem>
    )
  }, {})

  // const accessoriesMarkup = accessories.map((acc) => (
  //   <div key={acc?.id} className="accessory">
  //     {/* <Link to={"/accessory-categories/" + acc?.id} className="category-name"> */}
  //     <span className="accessory-name">{acc?.name}</span>
  //     <span className="accessory-cost">
  //       {acc?.rrp ? formatCurrency(acc?.rrp) : "N/A"}
  //     </span>
  //     {/* </Link> */}
  //   </div>
  // ))

  return (
    
    <div className={cx("accessories-accordion-container")} >
      {/* <div className="model-image">
        <img src={process.env.REACT_APP_ASSETS_URL + image?.url} alt="" />
      </div> */}
      <h3>Add Accessories</h3>
      <p className="weak-label">Add accessories to create your perfect product</p>
    
      <Accordion>
        {catsMarkup}
      </Accordion>
    </div>
  )
}
