import React from "react"
import "./StepTitle.css"
import cx from "classnames"

type StepTitleProps = {
  title: string
  isActive?: boolean
  showBackButton?: boolean
  onBackClick?: Function
}

export default function StepTitle(props: StepTitleProps) {
  let backLink

  if (props.showBackButton) {
    backLink = (
      <span
        className="step-back"
        onClick={() => props.onBackClick && props.onBackClick()}
      >
        {"< "}{" "}
      </span>
    )
  }

  return (
    <div className={cx("step-title", { active: props.isActive })}>
      {backLink} <h2>{props.title}</h2>
    </div>
  )
}
